import { Competition } from "@prisma/client";
import { CategoryEntity } from "../category/category.entity";
import { IDomainEvent } from "@esport.monorepo/contracts";


export class CompetitionEntity implements Competition {
  id: number;
  title: string;
  dateStart: Date;
  dateEnd: Date;
  organisationId: number;

  categories?: CategoryEntity[];

  events: IDomainEvent[] = [];
  
  constructor(partial: Partial<CompetitionEntity>) {
    this.id = partial.id;
    this.title = partial.title;
    this.dateStart = this.updateDate(partial.dateStart);
    this.dateEnd = this.updateDate(partial.dateEnd);
    this.organisationId = partial.organisationId;
    this.categories = partial.categories;
  }

  updateDate(date?: Date | string) {
    if (date) {
      return new Date(date);
    }
  }
  
  addEvent(event: IDomainEvent) {
    this.events.push(event);
    return this;
  }
}